.Container {
    height: 100vh;
    display: flex;
    padding: 5%;
    
}

.Box1 {
    flex: 1;
    padding: 5%;
    margin: auto;
}

.Box1 img {
    max-width: 100%;
    max-height: 100%;
    box-shadow:0 6px 15px rgb(71, 71, 71);
}

.Box2 {
    flex: 3;
   margin: auto;
}

.Box2 h2{
    font-size: 2rem;
    font-family: 'Poiret One', cursive;
    font-weight: lighter;
    text-align: center;

}
.Box2 p{
    font-size: 1rem;
    font-family: 'Raleway', sans-serif;
    letter-spacing: 1px;
    padding-top: 50px;
}









