.Nav-container{
    font-family: 'Poiret One', cursive;
    display: flex;    
    background: transparent;
    position: sticky;
    top: 0;
    z-index: 10;
    justify-content: center;
    height: 80px;
}

nav{
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: auto;
    align-items: center;
}



nav h2 {
    letter-spacing: 3px;
    cursor: pointer;
    color: gray;
    /* display: flex; */
    /* flex: 2; */
}
nav h2 span{
    color: rgb(180, 180, 180);
}

nav ul {
    display: flex;
    text-decoration: none;
    list-style: none;
}
nav ul  a{
    text-decoration: none;
    padding: 2rem;
    color: white;
    letter-spacing: 0.15em;
    cursor: pointer;

}

nav ul li > .active {
    color:rgb(179, 179, 179);
}

.Nav-container.active {
    background: rgb(20, 20, 20);
    transition: all 0.4s ease-in-out;
    
}